import { openDB } from 'idb';

const DB_NAME = 'restaurant-catalog';
const STORE_NAME = 'favorites';

async function initDB() {
  return openDB(DB_NAME, 1, {
    upgrade(db) {
      if (!db.objectStoreNames.contains(STORE_NAME)) {
        db.createObjectStore(STORE_NAME, { keyPath: 'id' });
      }
    },
  });
}

export async function getFavoriteRestaurants() {
  const db = await initDB();
  return db.getAll(STORE_NAME);
}

export async function getFavoriteRestaurant(id) {
  const db = await initDB();
  return db.get(STORE_NAME, id);
}

export async function addFavoriteRestaurant(restaurant) {
  const db = await initDB();
  return db.put(STORE_NAME, restaurant);
}

export async function removeFavoriteRestaurant(id) {
  const db = await initDB();
  return db.delete(STORE_NAME, id);
}

export async function toggleFavoriteRestaurant(restaurant) {
  const db = await initDB();
  const existing = await db.get(STORE_NAME, restaurant.id);
  if (existing) {
    return db.delete(STORE_NAME, restaurant.id);
  }
  return db.put(STORE_NAME, restaurant);
}

export async function isFavorite(id) {
  const db = await initDB();
  const favorite = await db.get(STORE_NAME, id);
  return Boolean(favorite);
}
