// ./src/scripts/detail.js

import { API_BASE_URL } from './config';
import {
  isFavorite,
  toggleFavoriteRestaurant,
} from './indexeddb';

async function fetchRestaurantDetail(id) {
  try {
    const response = await fetch(`${API_BASE_URL}/detail/${id}`);
    const data = await response.json();
    displayRestaurantDetail(data.restaurant);
    window.history.pushState({}, '', `/?restaurant=${data.restaurant.id}`);
  } catch (error) {
    console.error('Failed to fetch restaurant details:', error);
    const restaurant = await getCachedRestaurant(id);
    if (restaurant) displayRestaurantDetail(restaurant);
  }
}

async function displayRestaurantDetail(restaurant) {
  const restaurantDetail = document.querySelector('#restaurant-detail');
  restaurantDetail.innerHTML = `
    <div class="detail-header">
      <img src="${API_BASE_URL}/images/large/${restaurant.pictureId}" alt="${restaurant.name}">
      <div>
        <h1>${restaurant.name}</h1>
        <p><strong>Address:</strong> ${restaurant.address}</p>
        <p><strong>City:</strong> ${restaurant.city}</p>
        <p>${restaurant.description}</p>
      </div>
    </div>
    <h2>Menus</h2>
    <h3>Foods</h3>
    <ul>${restaurant.menus.foods.map((food) => `<li>${food.name}</li>`).join('')}</ul>
    <h3>Drinks</h3>
    <ul>${restaurant.menus.drinks.map((drink) => `<li>${drink.name}</li>`).join('')}</ul>
    <h2>Customer Reviews</h2>
    <ul class="review-list">${restaurant.customerReviews.map((review) => `
      <li>
        <p><strong>${review.name}</strong> (${review.date})</p>
        <p>${review.review}</p>
      </li>
    `).join('')}</ul>
    <button id="favorite-button" data-id="${restaurant.id}">Add to Favorites</button>
  `;

  const favoriteButton = document.getElementById('favorite-button');
  const isFav = await isFavorite(restaurant.id);
  favoriteButton.textContent = isFav ? 'Remove from Favorites' : 'Add to Favorites';

  favoriteButton.addEventListener('click', async () => {
    await toggleFavoriteRestaurant(restaurant);
    const isFav = await isFavorite(restaurant.id);
    favoriteButton.textContent = isFav ? 'Remove from Favorites' : 'Add to Favorites';
  });

  document.getElementById('home-section').classList.remove('active');
  document.getElementById('detail-section').classList.add('active');
  document.getElementById('favorite-section').classList.remove('active');
}

export function loadDetailPage(id) {
  fetchRestaurantDetail(id);
}

async function getCachedRestaurant(id) {
  const cache = await caches.open('restaurant-data');
  const response = await cache.match(`${API_BASE_URL}/detail/${id}`);
  if (!response) return null;
  const data = await response.json();
  return data.restaurant;
}
